body, h1, h2, h3, h4, h5, h6, p, ul, ol, table, dl, dd {
  margin: 0;
  padding: 0;
}
h1 {
  font-size: 16px;
}
a {
  text-decoration: none;
  color: #000;
}
ul, ol {
  list-style: none;
}
img {
  max-width: 100%;
  vertical-align: top;
  border: none;
}
body {
  font-family: "Roboto" , "Bebas Neue", "Noto Sans KR", sans-serif, "Playfair", "Oranienbaum", serif;
  font-size: 1em;
  scroll-behavior: smooth;
}
body.stop-scrolling {
  height: 100%;
  overflow: hidden;
}
::selection {
  background-color: rgba(0, 0, 0, .3);
  color: #fff;
}
.dark ::selection {
  background-color: rgba(255, 255, 255, .8);
  color: #000;
}
body {
  transition: 0.3s;
  background: #FBFCF6;
}
body.dark {
  background-color: #000;
}
#main {
  height: 100vh;
  position: relative;
}
#main .top {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 5em;
  padding: 0 6vw;
  z-index: 1000;
  box-sizing: border-box;
}
#main a.tab {
  display: none;
  position: relative;
  width: 40px;
  height: 25px;
}
#main a.tab span {
  position: absolute;
  right: 0;
  top: 0;
  height: 1px;
  background-color: #000;
  transition: 0.3s;
}
#main a.tab span:nth-child(1) {
  width: 100%;
}
#main a.tab span:nth-child(2) {
  top: 10px;
  width: 80%;
}
#main a.tab span:nth-child(3) {
  top: 20px;
  width: 70%;
}
#main a.tab.open span:nth-child(1) {
  top: 10px;
  width: 30px;
  transform: rotate(45deg);
}
#main a.tab.open span:nth-child(2) {
  display: none;
}
#main a.tab.open span:nth-child(3) {
  top: 10px;
  width: 30px;
  transform: rotate(-45deg);
}
.dark #main a.tab span {
  background-color: #FBFCF6;
}
.dark #main h1,
.dark header.top h1 {
  color: #FBFCF6;
}
header.top h1 {
  color: #000;
}
nav#gnb ul {
  display: flex;
  margin: 0 auto;
}
nav#gnb ul li {
  padding: 0 1.125em;
}
.dark nav#gnb ul li.active a  {
  color: #fff;
}
nav#gnb ul li a {
  display: block;
  line-height: 5em;
  font-weight: 500;
  color: #888;
}
nav#gnb ul li:last-child{
  padding-right: 0;
}
nav#gnb ul li.active a {
  color: #000;
}
.dark nav#gnb ul li.active a {
  color: #FBFCF6;
}
