#main .keytext_top {
  position: absolute;
  left: 6vw;
  top: 50%;
  transform: translateY(-50%);
  white-space: nowrap;
  font-family: "Bebas Neue";
} 
#main .text_wrap .inline .text_title {
  font-size: 10vw;
  text-rendering: optimizeLegibility;
  position: relative;
  color: #000;
  font-weight: 400;
  line-height: 0.9;
}
#main .keytext_top .wrap {
  display: flex;
  align-items: flex-end;
}
#main .text_wrap {
  width: fit-content;
}
#main .text_wrap .inline {
  overflow: hidden;
}
#main .text_wrap .inline .text_title span.slide-up {
  display: inline;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  transform: translateY(100%);
  transition: transform 1s ease;
  color: #000;
  text-align: center;
}
#main .text_wrap .inline .text_title span.slide-up {
  color: #FBFCF6;
}
#main .text_wrap:hover .text_title span.slide-up,
#main .text_wrap.active .text_title span.slide-up {
  transform: translateY(0);
}
#main .text_wrap .text_title span.slide-up::before {
  display: inline;
  position: absolute;
  top: 0;
  left: 0;
  content: attr(data-content);
  color: #B2DB33;
  transform: translateY(-100%);
  transition: transform 1s ease;
}
#main .text_wrap:hover .text_title span.slide-up::before,
#main .text_wrap.active .text_title span.slide-up::before
{
  transform: translateY(0);
}
.second span {
  display: inline-block;
  color: #c1c1c1;
  font-size: 10vw;
  line-height: 0.9;
  padding-left: 0.1875em;
}
.marquee-snap {
  margin-top: -8px;
  height: calc(9vw + 12px);
  width: fit-content;
  overflow: hidden;
}
.marquee-snap .marquee_track-snap {
  width: 100%;
  height: 90%;
}
.marquee-snap .marquee_track-snap .marquee_item-snap {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: fit-content;
  align-items: center;
  height: 100%;
  text-align: left;
}
.marquee-snap .marquee_track-snap .marquee_item-snap h2 {
  font-family: "Playfair";
  font-size: 9vw;
  text-rendering: optimizeLegibility;
  font-weight: 400;
  line-height: 0.8;
  color: #000;
}
#main .keytext_bottom {
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 6vw;
  bottom: 5vh;
  text-align: right;
  word-break: keep-all;
}
nav#mobile {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #9999;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  font-family: "Bebas Neue";
}
nav#mobile ul {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}
nav#mobile ul li a {
  font-size: 4em;
  color: #888;
}
.dark nav#mobile ul li a {
  color: #c5c5c5;
}
nav#mobile ul li.active a {
  color: #fff;
}
.dark nav#mobile ul li.active a {
  color: #B2DB33;
}
@media only screen and (max-width:1280px) {
  body {
    font-size: 0.9375em;
  }
  .marquee-snap {
    height: 12vw;
  }
  #main .text_wrap .inline .text_title,
  .second span {
    font-size: 12vw;
  }
  .marquee-snap .marquee_track-snap .marquee_item-snap h2 {
    font-size: 11vw;
  }
}
@media only screen and (max-width:940px){
