    font-size: 18vw;
    padding: 0;
  } 
  .marquee-snap .marquee_track-snap .marquee_item-snap h2 {
    font-size: 17vw;
  }
  @media screen and (orientation: landscape) {
    nav#mobile ul li a {
      font-size: 10vh;
    }
    #main .text_wrap .inline .text_title,
    h2,
    #main .text_wrap .inline .text_title,
    .second span {
      font-size: 20vh;
    } 
    .marquee-snap .marquee_track-snap .marquee_item-snap h2 {
      font-size: 19vh;
    }
    .marquee-snap {
      height: 19vh;
    }
  }
}
