  font-size: 1.725em;
  white-space: nowrap;
  position: relative;
}
section ul li h3::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  width: 0;
  height: 0.5em;
  background-color: #B2DB33;
  z-index: -1;
  transition: 0.3s;
}
section ul li:hover h3::after {
  width: 100%;
}
section br {
  display: none;
}
.works_list section .sec-inner {
  padding: 0 10vw;
}
#project ul {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 3em;
}
#project ul li a .card .card_image {
  position: relative;
  overflow: hidden;
  padding: 3em 3em 0 3em;
  aspect-ratio: 16/9;
}
#project ul li a .card .card_image img {
  width: 100%;
  height: 100%;
  object-position: top;
}
#project ul li a span.background {
  position: absolute;
  background-size: cover;
  background-position: bottom;
  filter: blur(20px);
  z-index: -1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
#project ul li a .card .card_image .mockup {
  position: relative;
  transition: 0.2s;
  width: 100%;
  height: 100%;
}
#project ul li:hover a .card .card_image .mockup {
  transform: scale(1.05);
}
#project ul li a .card .card_image .mockup .desktop {
  position: absolute;
  left: 0;
  border-top: 3px solid #000;
}
#project ul li a .card .card_image .mockup .mobile {
  position: absolute;
  bottom: -10%;
  right: -1em;
  width: 20%;
  border-radius: 0.5em;
  overflow: hidden;
  outline: 2px solid #000;
}
#project ul li a .card .card_content {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  height: max-content;
}
#project ul li a .card .card_content .stack {
  display: flex;
  flex-wrap: wrap;
  gap: 4px;
}
#project ul li a .card .card_content .stack span {
  display: inline-block;
  background-color: #a9a9a9;
  font-size: 0.75em;
  padding: 2px 5px;
  border-radius: 3px;
  color: #fff;
  line-height: 1.25em;
}
/*open source*/
#open_source .open-swiper {
  position: relative;
  padding: 5em 0;
  overflow-x: hidden;
}
#open_source .open-swiper .swiper-slide {
  border: 1px solid #000;
  border-top: 2em solid #000;
  height: 20em;
}
#open_source .open-swiper .swiper-slide .desc {
  height: 100%;
  position: relative;
  padding: 10px;
  box-sizing: border-box;
}
#open_source .open-swiper .swiper-slide .desc strong {
  font-size: 2em;
  position: relative;
  line-height: 1.2em;
}
#open_source .open-swiper .swiper-slide .desc strong::after {
  position: absolute;
  content: "";
  right: -15px;
  top: 3px;
  height: 8px;
  width: 8px;
  border-radius: 50%;
  background-color: #d9d9d9;
  transition: 0.2s;
}
#open_source .open-swiper .swiper-slide.swiper-slide-active .desc strong::after,
#open_source .open-swiper .swiper-slide:hover .desc strong::after {
  background-color: #B2DB33;
}
#open_source .open-swiper .swiper-slide .desc p {
  position: absolute;
  top: 50%;
  text-align: justify;
  padding-right: 10px;
}
#open_source .open-swiper .swiper-slide .desc .link {
  display: flex;
  text-transform: uppercase;
  position: absolute;
  bottom: 10px;
  gap: 5px;
}
#open_source .open-swiper .swiper-slide .desc .link a {
  height: max-content;
  width: 40px;
  padding: 0 5px;
  border-radius: 10px;
  border: 1px solid #555;
  color: #555;
  text-align: center;
  transition: 0.2s;
}
#open_source .open-swiper .swiper-slide .desc .link a:hover {
  border: 1px solid #B2DB33;
  background-color: #B2DB33;
  color: #fff;
}
#open_source .open-swiper .swiper-slide .desc p span {
  font-weight: 600;
}
#open_source .open-swiper .swiper-button-next,
#open_source .open-swiper .swiper-button-prev {
  display: inline;
  top: 30px;
  bottom: 0;
  left: auto;
}
#open_source .open-swiper .swiper-button-next {
  right: 0;
  width: 4em;
  height: 2em;
  background: url(./images/arrow.svg) no-repeat;
  background-size: contain;
}
#open_source .open-swiper .swiper-button-prev {
  right: 80px;
  width: 4em;
  height: 2em;
  background: url(./images/arrow.svg) no-repeat;
  background-size: contain;
  transform: rotate(180deg);
}
#open_source .open-swiper .swiper-button-next::after,
#open_source .open-swiper .swiper-button-prev::after {
  content:"";
}

#casestudy ul {
  margin-top: 3em;
}
#casestudy ul li {
  border-bottom: 1px solid #888;
  overflow: hidden;
}
#casestudy ul li strong.brand {
  display: inline-block;
  width: 100%;
  font-size: 2em;
  cursor: pointer;
  position: relative;
  padding: 20px 0;
}
#casestudy ul li strong.brand::after {
  position: absolute;
  content: "";
  height: 0.725em;
  width: 0.725em;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background: url(./images/more.svg) no-repeat;
  background-size: cover;
  transition: 0.2s;
}
#casestudy ul li.active strong.brand::after {
  transform: translateY(-50%) rotate(45deg);
}
#casestudy ul li .hide_desc {
  display: none;
  height: 0;
}
#casestudy ul li .hide_desc p {
  width: fit-content;
  text-align: justify;
}
#casestudy ul li .hide_desc a {
  display: inline-block;
  background: url(./images/casestudy_arrow.svg) no-repeat;
  background-position: center right 0.75em;
  background-size: 0.75em;
  padding: 20px 1.8em 20px 0;
  transition: 0.3s;
}
.dark #casestudy ul li .hide_desc a {
  display: inline-block;
  background: url(./images/casestudy_arrow_white.svg) no-repeat;
  background-position: center right 0.75em;
  background-size: 0.75em;
  padding: 20px 1.8em 20px 0;
  color: #fff;
  transition: 0.3s;
}
#casestudy ul li .hide_desc a:hover {
  background-position: 100%;
}
#casestudy ul li .hide_desc a span {
  font-weight: 500;
  line-height: 2em;
}
.dark #casestudy ul li {
  border-bottom: 1px solid #444;
}
.dark #casestudy ul li strong.brand {
  color: #FBFCF6;
}
.dark #casestudy ul li strong.brand::after {
  filter: invert(100);
}
.dark #casestudy ul li .hide_desc p {
  color: #FBFCF6;
}

@media only screen and (max-width:1280px) {
  .works_list section .sec-inner {
      padding: 0 8vw;
  }
  #project ul {
      grid-gap: 30px 20px;
  }
  #project ul li a .card .card_image {
      padding: 2em 2em 0 2em;
  }
  #casestudy ul li strong.brand {
      font-size: 1.725em;
  }

}
@media only screen and (max-width:940px) {
  .works_list section .sec-inner {
      padding: 0 6vw;
  }
  #project ul {
      grid-gap: 30px 10px;
  }
}
@media only screen and (max-width:720px) {
  section br {
      display: block;
  }
  .works_list section .sec-inner {
      padding: 0 30px;
  }
  
  section ul.project > li .heading {
      width: 70%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 2em;
  }
  #project ul {
      grid-template-columns: repeat(1, 1fr);
  }
  #project ul li a .card .card_image {
      padding: 3em 3em 0 3em;
  }
}
@media only screen and (max-width:499px) {
  #casestudy ul li strong.brand {
      font-size: 1.875em;
  }
  #project ul li a .card .card_image {
      padding: 2em 2em 0 2em;
  }
}